.LeftSidebar {
    width: 104px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 24px;
    align-items: center;
    box-sizing: border-box;
    background: #1F1D2B;
    border-radius: 16px;

    .left-link {
        display: block; 
        color: #fff;
        width: 56px;
        height: 56px;
        text-decoration: none;
        background: #1F1D2B;
        border-radius: 8px;
        box-sizing: border-box;
        padding: 18px;

        &.settings {
            margin-bottom: 69px;
        }

        &.logo {
            background: rgb(235, 150, 106, 0.26);
            padding: 8px;
            border-radius: 12px;
            margin-bottom: 26px;
        }
    
        &:focus {
            background: #EA7C69;
            box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
            border-radius: 8px;
            fill: #fff;
        }
    }
}
