.main__dashboard-left {
    width: 60%;
    padding: 24px;
    padding-bottom: 0;
    box-sizing: border-box;
}

.top {

    &__left-dashboard {
        padding-bottom: 24px;
        border-bottom: 1px solid #393C49;
        margin-bottom: 24px;          
        box-sizing: border-box;                                      
    }

    &__left-dashboard-heading {
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 140%;  
        color: #FFFFFF;
    }

    &__left-dashboard-date {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 140%;
        color: #ABBBC2;
    }
}

.center__dashboard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
}   

.user-main {
    background: #1F1D2B;
    border-radius: 8px;
}

.user-header {
    padding: 24px;
    box-sizing: border-box;
    border-bottom: 1px solid #393C49;

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 16px;
    }

    &__bottom {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }
}

.user-main-heading {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: #FFFFFF;
}

.user-filter-btn {
    background: #1F1D2B;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 14px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__img {
        margin-right: 11px;
    }
}

.user-table-titles {
    font-style: normal;
    font-weight: 600;
    width: 30%;
    font-size: 14px;
    line-height: 140%;
    color: #FFFFFF;

    &.total {
        width: 25%;
    }

    &:last-child {
        width: 5%;
    }
}

.dashboard-main {
    padding: 16px 24px;
    padding-bottom: 0;
    height: 270px;
    overflow: scroll;
    box-sizing: border-box;

    &::-webkit-scrollbar {
        display: none;
    }
}
