body {
    margin: 0;
    padding: 0;
    font-family: 'Barlow', sans-serif;
    background-color: #252836;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}


.container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
    box-sizing: border-box;
}

.App {
    display: flex;
}