.main__payment {

    .modal{
        padding: 24px;
        padding-top: 34px;
        background-color: rgba(31, 29, 43, 1);
        height: 100%;
        box-sizing: border-box;
        width: 0;
        position: absolute;
        right: 0;
        top: 0;
        opacity: 0;
        transition: all ease 0.5s;
        overflow: hidden;
        z-index: 1234;
    }

    .overlay{
        height: 100vh;
        opacity: 0;
        position: absolute;
        overflow: hidden;
        top: 0;
        left: 0;
        width: 0;
        z-index: 123;
    }

    &.actipayment {
        .overlay {
            width: 100vw;
            opacity: 1;
            background-color: rgba(0, 0, 0, 0.5);
            overflow: hidden;
        }

        .modal {
            overflow: hidden;
            opacity: 1;
            // display: block;
            width: 29%;
        }
    }
}

.main__payment-top {
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #393C49;

}

.payment{
    &__top-text {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: #ABBBC2;
    }
    
    &__top-heading {
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        margin-bottom: 8px;
        line-height: 140%;
        color: #FFFFFF;
    }

    &__center-heading {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: #FFFFFF;
    }

    &__center-name {
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        margin-bottom: 8px;
        color: #FFFFFF;
    }

    &__center-input {
        background: #2D303E;
        padding: 14px;
        width: 100%;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
        margin-bottom: 16px;

        &.date {
            width: 90%;
            margin: 0;
        }

        &.password {
            width: 100%;
            margin: 0;
        }
    }

    &__center-dates-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 16px;
        border-bottom: 1px solid #393C49;
        margin-bottom: 16px;
    }

    &__center-order-wrapper {
        display: flex;
        justify-content: space-between;
        margin-bottom: 80px;
        align-items: flex-start;
    }

    &__middle-select-payment {
        background: #1F1D2B;
        width: 100%;
        height: 48px;
        padding: 15px;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #E0E6E9;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
    }

    &__buttons {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.buttons__btn-payment {
    width: 50%;
    padding: 14px 34px;
    background: #EA7C69;
    box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
    border-radius: 8px;
    color: #FFFFFF;

    &.cancel {
        background: #1F1D2B;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        box-shadow: none;
        width: 45%;
        color: #fff;
    }
}

.left-payment-so {
    width: 45%;
}