.dashboard-top {
    padding: 24px;
    box-sizing: border-box;
    background: #1F1D2B;
    border-radius: 8px;
    margin-bottom: 24px;
    width: 100%;
}

.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 24px;
    border-bottom: 1px solid #393C49;

    &__heading {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: #FFFFFF;
    }
}

#dashboard-select {
    background: #1F1D2B;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 14px;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #FFFFFF;
}

.button {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    color: #EA7C69;
    width: 100%;
    background: #1F1D2B;
    border: 1px solid #EA7C69;
    padding: 14px;
    border-radius: 8px;
}

.dashboard-main {
    padding: 32px;
    box-sizing: border-box;
}