.foods-item {
    width: 30%;
    box-sizing: border-box;
    padding: 24px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    background: #1F1D2B;
    border-radius: 16px;
    position: relative;
    text-align: center;
    margin-top: 40px;
    height: 226px;
    margin-bottom: 58px;

    &__img {
        position: absolute;
        top: -37px;

    }

    .food-name {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        width: 70%;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 8px;
    }

    .food-cost {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 4px;
    }

    .available-food {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: #ABBBC2;
    }
}