.food-item {
    display: flex;
    align-items: center;
    padding: 20px 0;

    &__img {
        margin-right: 24px;
    }
}

.food-titles {
    &__title {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #E0E6E9;
    }

    &__number {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 140%;
        color: #ABBBC2;
    }
}

.most-ordered {
    height: 200px;
    overflow: scroll;

    &::-webkit-scrollbar {
        display: none;
    }
}