.center__items-home {
    padding-top: 24px;
}

.items{
    &__top-home {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
    }

    &__center-home {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__img-home {
        margin-right: 6px;
    }

    &__text-home-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__text-home-right {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #FFFFFF;
    }

    &__number {
        background: #2D303E;
        width: 48px;
        height: 48px;
        text-align: center;
        padding-top: 12px;
        border: 1px solid #393C49;
        box-sizing: border-box;
        margin-right: 21px;
        border-radius: 8px;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: #FFFFFF;
    }

    &__price {
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 140%;
        color: #ABBBC2;
    }

    &__price-text {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        text-align: right;
        color: #FFFFFF;
    }

    &__center-input {
        padding: 14px;
        width: 100%;
        margin-right: 15px;
        background: #2D303E;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
    }
}

.second__wrapper-home {
    display: flex;
    justify-content: space-between;
    align-items: center;
}