.scroll-btn-wrapper {
    display: flex;
    // justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #393C49;
    margin-bottom: 24px;


    .scroll-link {
        padding: 13px 5px;
        border-bottom: 3px solid transparent;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        color: #FFFFFF;   
        margin-right: 32px;
        text-decoration: none;     
    
    &.first {
        padding-left: 0;
        border-bottom: 3px solid #EA7C69;
        color: #EA7C69;     
    }


        &:focus {
            border-bottom: 3px solid #EA7C69;
            color: #EA7C69;
        }
    }
}