.homepage {
    padding: 24px;
    padding-bottom: 0;
    max-width: 65%;
    width: 100%;
    box-sizing: border-box;

    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.header-right {
    position: relative;

}

.search-icon {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    left: 14px;
    
}

.search-holder {
    background: #2D303E;
    padding: 14px;   
    color: #fff;
    width: 100%;
    height: 100%;
    padding-left: 41px;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    position: relative;
}

.primary-heading {
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 140%;  
    color: #FFFFFF;
}

.header-date {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    color: #E0E6E9;
}