.center__dashboard-items {
    display: flex;
    justify-content: space-between;
    // align-items: center;
    flex-direction: column;
    background: #1F1D2B;
    width: 32%;
    border-radius: 8px;
    padding: 16px;
    box-sizing: border-box;
}

.items {

    &__top-dashboard {
        display: flex;
        // justify-content: space-between;
        align-items: center;
        margin-bottom: 8px;
    }

    &__img-price {
        margin-right: 12px;
        padding: 10px;
        box-sizing: border-box;
        background: #252836;
        border-radius: 8px;
    }

    &__rate {
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        margin-right: 6px;
        line-height: 140%;
        color: #50D1AA;
    }

    &__heading {
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 140%;
        margin-bottom: 8px;
        color: #FFFFFF;
    }

    &__text-dashboard {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #ABBBC2;
    }
}