input {
    color: #FFFFFF;
}

.right-side-bar {
    padding: 24px;
    background: #1F1D2B;
    position: absolute;
    top: 0;
    right: 0;
    overflow: hidden;
    width: 25%;
    border-radius: 8px;
}

.top{
    
    &__heading-home {
        margin-bottom: 24px;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: #FFFFFF;
    }
}

.top__bottons-home {
    padding: 7px 12px;
    margin-right: 8px;
    color: #EA7C69;
    background: #1F1D2B;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    margin-bottom: 24px;

    &:focus {
        background: #EA7C69;
        border-radius: 8px;
        color: #FFFFFF;
        outline: none;
    }
}

.right__center-home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 24px;
    border-bottom: 1px solid #393C49;
}

.wra {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.center__heading-home {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    color: #FFFFFF;

    &.middle {
        margin-right: 43px;
    }
}

.right__center {
    overflow-y: scroll;
    height: 326px;

    &::-webkit-scrollbar {
        visibility: visible;
    }
}

.bottom {

    &__top-home {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 17px;
        
        &.first{
            padding-top: 25px;
            border-top: 1px solid #393C49;
        }

        &.second {
            margin-bottom: 42px;
        }
    }

    &__top-text {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        color: #ABBBC2;
    }

    &__top-price {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        text-align: right;
        color: #FFFFFF;
    }

    &__button {
        background: #EA7C69;
        box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
        border-radius: 8px;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        color: #FAFAFA;
        width: 100%;
        text-align: center;
        padding: 14px 0;
    }
}