.user-item {
    display: flex;
    // justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
}

.customer {
    display: flex;
    align-items: center;
    width: 30%;

    &__img-dashboard {
        margin-right: 16px;

    }


    &__name-dashboard {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        color: #E0E6E9;
    }
}

.items__menu-dashboard {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    color: #E0E6E9;
    width: 30%;
}

.menu__text-dashboard {
    width: 137px;
}

.payment__price-dashboard {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    color: #E0E6E9;
}

.items__total-payment-dashboard {
    width: 25%;
}

.status__button-dashboard {
    background: rgba(107, 226, 190, 0.24);
    border-radius: 30px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #50D1AA;
    outline: none;
    padding: 4px 12px;
    box-sizing: border-box;
}