.food-link-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 22px;
}

.header-title {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: #FFFFFF;
}

.choose {
    background: #1F1D2B;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    width: 100px;
    height: 42px;
    padding-left: 15px;
    padding-right: 5px;
    color: #FFFFFF;
    box-sizing: border-box;
}